import bannerImg from "../../styles/Images/BPOimages/hero-img.png";
import React from "react";

const Banner = () => {
  return (
    <section className="section-hero h-100">
      <div className="container">
        <div className="row align-items-center d-flex">
          <div className="col-md-6">
            <div className="hero-text-box">
              <h1 className="bottom">
                A small business is only as good as its tools.
              </h1>
              <p className="bottom">
                We're different. Shield Infotech is the only business operation
                services platform that lets you run your business on one
                platform.
              </p>
              <p>
                <strong>Small Introduction</strong>
              </p>
              <p className="bottom">
                Business Process Outsourcing (BPO) refers to the practice of
                contracting specific business tasks or processes to third-party
                service providers. BPO services are often categorized into two
                main types: back-office and front-office outsourcing.
              </p>
              <div className="flexible">
                <button className="btn btn--full">Our Projects</button>
                <button className="btn btn--outline">Learn more &darr;</button>
              </div>
            </div>
          </div>
          <div className="col-md-6">
            <div className="hero-img-box">
              <img
                src={bannerImg}
                className="hero-img"
                alt="shield infotech logo"
              />
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Banner;
