import React from "react";
import img1 from "../../styles/Images/Projects/works1.jpg";
import img2 from "../../styles/Images/Projects/works2.jpg";
import img3 from "../../styles/Images/Projects/works3.svg";

const OurProjects = () => {
  return (
    <section className="section-projects" id="projects">
      <div className="bottomAnim container">
        <h4 className="bottomAnim center">Our Projects</h4>
        <h3>
          Discover the untapped potential of our business outsourcing services,
          delivered by our skilled global talents, to expedite your projects and
          disentangle critical challenges.
        </h3>
      </div>
      <div className="container grid grid--2-cols">
        <div className="project-img-box">
          <img style={{ width: "100%" }} src={img1} alt="img" />
        </div>
        <div className="project-content-box">
          <p className="bottomAnim ">
            Our works speak volumes about our commitment to excellence. With a
            diverse portfolio spanning industries, we have successfully tackled
            complex challenges and delivered tangible results for our clients.
          </p>
        </div>
        <div className="project-img-box">
          <img style={{ width: "100%" }} src={img2} alt="img" />
        </div>
        <div className="project-content-box">
          <p className="bottomAnim ">
            Whether it's providing top-notch customer support, streamlining data
            management, or optimizing back-office operations, our dedicated team
            of professionals goes above and beyond.
          </p>
        </div>
        <div className="project-img-box">
          <img
            style={{ width: "100%" }}
            src={img3}
            alt="img"
          />
        </div>
        <div className="project-content-box">
          <p className="bottomAnim ">
            Explore our works, and you'll find a trail of satisfied clients,
            streamlined processes, and enhanced efficiency. Your success is our
            work in progress, and we're ready to take your business to the next
            level.
          </p>
        </div>
      </div>
    </section>
  );
};

export default OurProjects;
