import React from "react";

const AboutUs = () => {
  return (
    <section className="section-how" id="about">
      <div className="container">
        <h4 className="center">About Us</h4>
        <h3 className="center">
          SHIELD INFOTECH is a BPO (Business Process Outsourcing) Company
        </h3>
        <h3 className="center">
          Business process outsourcing (BPO) is a method of subcontracting
          various business-related operations to third-party vendors.
        </h3>
      </div>
      <div className="container">
        <div className="row">
          <div className="col-md-4">
            <div className="flex">
              <h4>Business Processing</h4>
              <p>
                Back office BPO refers to a company contracting its core
                business support operations such as accounting, payment
                processing, IT services, human resources, regulatory compliance,
                and quality assurance to outside professionals who ensure the
                business runs smoothly.
              </p>
            </div>
          </div>
          <div className="col-md-4">
            <div className=" flex">
              <h4>Special Considerations</h4>
              <p>
                ﻿The breadth of a business' BPO options depends on whether it
                contracts its operations within or outside the borders of its
                home country. BPO is deemed "offshore outsourcing" if the
                contract is sent to another country where there is political
                stability, lower labor costs, and/or tax savings.
              </p>
            </div>
          </div>
          <div className="col-md-4">
            <div className=" flex">
              <h4>The Attraction of BPO</h4>
              <p>
                BPO offers businesses access to innovative technological
                resources that they might not otherwise have exposure to. BPO
                partners and companies constantly strive to improve their
                processes by adopting the most recent technologies and
                practices.
              </p>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default AboutUs;
